	module.exports = " <div id=receive> <h2>Receive a Secret</h2> <div class=row v-show=activeReceivedSecretPresent> <div class=col-md-12> <div class=\"panel panel-success\"> <div class=panel-heading> <h3 class=panel-title>Secret : <span class=text-muted>{{ activeReceivedSecretId }}</span></h3> <br> <p class=\"text-muted text-small\">{{ activeReceivedSecretCreatedAt }} UTC</p> </div> <div class=panel-body> <pre class=pre-scrollable>{{ activeReceivedSecretPlaintext }}</pre> </div> </div> </div> </div> <div class=row v-show=!activeReceivedSecretPresent> <div class=col-md-12> <div class=jumbotron> <h3 class=empty-title>No secret selected</h3> <p class=empty-meta v-show=receivedSecretsPresent>You have {{ receivedSecretsCount }} secrets linked in the received secrets section below.</p> <p class=empty-meta>Like to send a new one?</p> <button v-link=\"{ path: '/e' }\" class=\"empty-action btn btn-primary\">Send New Secret</button> </div> <div class=empty> </div> </div> </div> <br> <br> <div class=row v-show=receivedSecretsPresent> <div class=col-md-12> <strong>Received ( <a @click=deleteAllReceivedSecrets>delete all</a> )</strong> <table class=\"table table-striped table-hover\"> <caption> These secrets were previously received, decrypted locally, and stored for your convenience. To protect the sender, and yourself, you should delete any secrets you no longer need access to. These secrets are unencrypted and this is <em>NOT</em> a secure storage area! </caption> <thead> <tr> <th>ID</th> <th>Received At</th> <th>Delete</th> </tr> </thead> <tbody> <tr v-bind:class=\"{ 'success': this.$route.params.id === secret.id }\" v-for=\"secret in receivedSecrets | orderBy 'receivedAt' -1\"> <td><a @click=setActiveReceivedSecret(secret) v-link=\"{ name: 'receive-id-key', params: { id: secret.id, key: secret.keyB32 }}\" class=\"btn btn-link\">{{ $key }}</a></td> <td>{{ (new Date(secret.receivedAt)).toLocaleString() }}</td> <td><a @click=deleteReceivedSecret(secret)>delete</a></td> </tr> </tbody> </table> </div> </div> </div> ";

