	module.exports = " <div id=send> <h2>Send a Secret</h2> <div class=row v-show=!activeSecretEncrypted> <div class=col-md-12> <textarea id=inputSecretText :value=activeSecretPlaintext @input=\"updateActiveSecretPlaintext | debounce 250\" v-bind:disabled=activeSecretEncrypted type=text class=form-control placeholder=\"We all have secrets. Share yours safely.\" rows=5 autofocus></textarea> <div class=progress> <div class=progress-bar v-bind:class=\"{ 'progress-bar-info': secretQuotaPercentDisplayClass === 'info', 'progress-bar-success': secretQuotaPercentDisplayClass === 'success', 'progress-bar-warning': secretQuotaPercentDisplayClass === 'warning', 'progress-bar-danger': secretQuotaPercentDisplayClass === 'danger' }\" role=progressbar :aria-valuenow=secretQuotaPercentNum aria-valuemin=0 aria-valuemax=100 style=\"min-width: 2.75em\" v-bind:style=\"{width: secretQuotaPercentString}\"> {{ secretQuotaPercentString }} </div> </div> </div> </div> <br> <div class=row v-show=!activeSecretEncrypted> <div class=col-xs-12> <div class=text-right> <button class=\"btn btn-danger\" v-on:click=unsetActiveSecret :disabled=disableSecretSubmit data-toggle=tooltip data-placement=bottom title=\"Clear the secret you've entered and don't submit anything\">Clear</button> <button class=\"btn btn-default\" v-on:click=encryptActiveSecret :disabled=disableSecretSubmit data-toggle=tooltip data-placement=bottom title=\"Encrypt and submit your secret, and display a link you can share\">Encrypt + Submit</button> </div> </div> </div> <div class=row v-show=activeSecretEncrypted> <div class=col-md-12> <h4>Share Your Encrypted Secret Now</h4> <p>Your secret has been securely encrypted with a <em>very strong</em> random encryption key and stored in the server Vault database. A private link has been generated that contains the ID and decryption key needed to locate and unlock your secret. The decryption key is <em>never</em> written to disk and this link will be destroyed once you leave this page. You should immediately copy this link and share it with your recipient. The encrypted secret will be destroyed immediately when this link is clicked and the encrypted data is viewed. If you try to 'test' the link you will also destroy the secret and prevent your recipient from viewing it as well. The secret will be permanently destroyed after 24 hours whether accessed or not.</p> </div> <div class=col-md-12> <p><a v-if=debug v-link=\"{ name: 'receive-id-key', params: { id: activeSecret.id, key: activeSecret.keyB32 }}\">dev link</a></p> <pre class=text-center>{{ activeSecretUrl }}</pre> <div class=text-center> <button class=\"btn btn-danger\" v-on:click=unsetActiveSecret :disabled=!activeSecretPlaintext data-toggle=tooltip data-placement=bottom title=\"Permanently clear the secret and the private link to it\">Clear Secret and Link</button> </div> </div> </div> <br> <br> <div class=row v-show=sentSecretsPresent> <div class=col-md-12> <strong>Receipts ( <a @click=deleteAllSentSecrets>delete all</a> )</strong> <table class=\"table table-striped table-hover\"> <caption> Receipts for secrets previously sent. These serve as a reminder of what was sent and cannot be used to decrypt or view a secret. You can however use a receipt to delete a secret stored on the server which has not yet expired or been viewed. </caption> <thead> <tr> <th>ID</th> <th>Created At</th> <th>Delete</th> </tr> </thead> <tbody> <tr v-bind:class=\"{ 'success': activeSecret.id === secret.id }\" v-for=\"secret in sentSecrets | orderBy 'createdAt' -1\"> <td>{{ $key }}</td> <td>{{ (new Date(secret.createdAt)).toLocaleString() }}</td> <td><a @click=deleteSentSecret(secret)>receipt</a><span class=text-muted> | </span><a @click=deleteServerSentSecret(secret)>both</a></td> </tr> </tbody> </table> </div> </div> </div> ";

